import DashboardRoute from '../../dashboard/route';
import ChartsRoute from '../../charts/route/ChartsRoute';
import CalenadrRoute from './../../calendar/route/index';
import FormRoute from './../../forms/route/FormRoute';
import mapRoute from '../../maps/route/mapRoute';

export const MainRoute = [
  {
    path: '/reactadminator/dashboard',
    component: DashboardRoute
  },
  {
    path: '/reactadminator/calendar',
    component: CalenadrRoute
  },
  {
    path: '/reactadminator/charts',
    component: ChartsRoute
  },
  {
    path: '/reactadminator/forms',
    component: FormRoute
  },
  {
    path: '/reactadminator/map',
    component: mapRoute
  },

  {path: '/reactadminator/', component: DashboardRoute, exact: true}
];
